* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  height: 100vh;
  width: 100%;
  background-color: white;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

header {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(153, 255, 153, 0.316);
    padding: 1.2rem 0;
    z-index: 1;   
    position: relative;
}

.header .whatsapp {
    background-color: rgba(0, 128, 0, 0.603);
    padding: 0.8rem;
    border-radius: 50%;
    cursor: pointer;
}

.header .logoImg {
    width: 4rem;
}

.header .whatsappIcon {
    width: 2rem;
}

.buttonSale .withBg{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 8px;
    padding: 10px;
    background-color: yellow;
}

.buttonSale .withBg:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.078);
    transition: 0.3s;
}

@media (max-width: 400px) {
    .buttonSale .withBg {
        padding: 8px 4px;
    }
}
.menu {
    width: 30%;
}

.menu .listLinks {
    display: flex;
    justify-content: center;
    grid-gap: 20%;
    gap: 20%;
}

.menu .listLinks a {
    color: black;
    font-size: 24px;
}

.menu .listLinks li:hover {
    border-bottom: 1px solid black;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 0.3s;
}

.navMenu {
    display: none;
}

@media (max-width: 601px) {
    .menu {
        display: none;
    }

    .navMenu {
        display: block;
    }

    .menuBurger {
        display: block;
        background-color: #0080007e;
        width: 48px;
        height: 48px;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        cursor: pointer;
        padding: 10px;
    }

    .burgerBar {
        width: 100%;
        height: 4px;
        background-color: white;
        border-radius: 8px;
    }
    nav {
        position: absolute;
        top: 1%;
        background-color: #f0f8ff;
        background-image: linear-gradient(#dbeeff, rgba(0, 128, 0, 0.262));
    }
    
    .navMenu {
        position: inherit;
    }

    .menuBar {
        width: 95vw;
        height: 100vh;
    }

    .hidden {
        display: none;
    }

    .visible {
        display: inherit;
    }

    .burgerBar.clicked:nth-child(1) {
        -webkit-transform: rotate(45deg) translate(30%, 200%);
                transform: rotate(45deg) translate(30%, 200%);
        transition: ease-out 0.5s;
    }

    .burgerBar.clicked:nth-child(2) {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
        transition: ease-out 0.5;
    }

    .burgerBar.clicked:nth-child(3) {
        -webkit-transform: rotate(135deg) translate(-32%, 220%);
                transform: rotate(135deg) translate(-32%, 220%);
        transition: ease-out 0.5;
    }

    .burgerBar.unclicked {
        -webkit-transform: rotate(0) translate(0);
                transform: rotate(0) translate(0);
        transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
    }

    .menuHamburger ul li{
        font-size: 24px;
        border-radius: 8px;
        background-color: #0080007e;
        background-image: linear-gradient(to right, #0080007e, #dbeeff);
        padding: 16px 8px;
        transition: 0.3s;
    }

    .menuHamburger li:hover {
        margin-left: 4px;
        -webkit-transform: scale(102%);
                transform: scale(102%);
        box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.078);
    }

    .menuHamburger li:active {
        -webkit-transform: scale(98%);
                transform: scale(98%);
    }

    .menuHamburger li a {
        color: white;
        font-weight: 600;
    }

    .menuHamburger .LinksHamburger{
        margin-top: 12px;
        display: flex;
        flex-direction: column;
        grid-gap: 8px;
        gap: 8px;
    }

}
.main {
    width: 100%;
    height: 150vh;
    background-image: url(/lift-detox-caps/static/media/LiftDetoxCaps.9ba305d3.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: 100% 100%;    
}

.main .imgDefault {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: relative;
    top: 16%;
    left: 24%;
}

@media (max-width: 1200px) {
    .main {
        width: 100%;
        height: 100vh;
        background-image: url(/lift-detox-caps/static/media/LiftDetoxCaps.9ba305d3.jpg);
        background-repeat: no-repeat;
        background-position: center top;
        background-size: 100% 100%;    
    }
} 

@media (max-width: 550px) {
    .main .imgDefault {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        position: relative;
        top: 16%;
        left: 8%;
    }
}

.about {
    width: 80%;
    display: flex;
    flex-direction: column;
    margin: auto;
    grid-gap: 32px;
    gap: 32px;
    text-align: center;
}

.about .mainTitle span{
    color: green;
}

.about .formula {
    margin: auto;
    text-align: center;
}

.about .formula h3 {
    margin-bottom: 40px;
}

.about .secretFormula {
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 48px;
    gap: 48px;
}

.about .secretFormula img {
    width: 100%;
}

@media (max-width: 600px) {
    .about {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .about h1 {
        width: 80%;
        text-align: center;
    }
}
.article {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 64px;
    grid-gap: 80px;
    gap: 80px;
}

.article h3 {
    margin-bottom: 8px;
}

.article .articleLeft .left {
    background-color: rgba(153, 255, 153, 0.188);
    max-width: 500px;
    border-radius: 8px;
    padding: 24px;
} 

.article .articleLeft {
    display: flex;
    justify-content: space-evenly;
}

.article .articleRight {
    display: flex;
    justify-content: space-evenly;
}

.article .articleRight .right {
    background-color: rgba(153, 255, 153, 0.188);
    max-width: 500px;
    border-radius: 8px;
    padding: 24px;
}

.article img {
    width: 10rem;
    border-radius: 8px;
    box-shadow: 10px 10px 6px rgba(0, 0, 0, 0.1);
}
.contact {
    margin-top: 80px;
    background-color: aliceblue;
    padding: 24px 0;
}

.contact span {
    color: green;
}

.contact h2 {
    text-align: center;
}

.contact form {
    margin-top: 48px;
    display: flex;
    flex-direction: column;
    margin-left: 16%;
}

.contact form label {
    margin-top: 24px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.contact form input {
    max-width: 350px;
    padding: 8px 0;
    border-radius: 8px;
    outline: none;
}

.contact form input:nth-child(8) {
    padding: 40px 0;
    text-align: unset;
    display: flex;
}

.contact form input:focus {
    box-shadow: 10px 10px 6px rgba(0, 0, 0, 0.1);
    margin-left: 6px;
    transition: 0.3s;
}

.contact textarea {
    max-width: 340px;
    border-radius: 8px;
    padding: 8px;
    outline: none;
    border: 2px solid black;
}

.contact form textarea:focus {
    box-shadow: 10px 10px 6px rgba(0, 0, 0, 0.1);
    margin-left: 6px;
    transition: 0.3s;
}

.contact form button {
    width: 6rem;
    padding: 12px 24px;
    font-size: large;
    background-color: yellow;
    border: none;
    cursor: pointer;
    border-radius: 8px;
    margin-top: 24px;
    transition: 0.3s;
}

.contact form button:hover {
    background-color: rgba(255, 255, 0, 0.422);
}

.contact form button:active {
    border: 2px solid rgba(0, 0, 0, 0.632);
}

@media (max-width: 601px) {
    .contact h2 {
        width: 80%;
        margin: auto;
    }

    .contact {
        display: flex;
        flex-direction: column;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contact .form  form{
        margin-left: 0;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .contact form input, textarea {
        width: 20rem;
    }
}
footer {
    margin-top: 48px;
    background-color: #99ff9940;
    padding: 64px 0;
}
footer .container{
    display: flex;
    justify-content: space-around;
    width: 80%;
    margin: auto;
}

footer h3 {
    margin-bottom: 24px;
}

footer ul {
    display: flex;
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;
}

footer li a {
    color: blue;
    text-decoration: none;
} 

footer li a:hover {
    color: rgba(0, 0, 255, 0.434);
}

footer .links {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}

footer img {
    width: 12rem;
}

footer .notifications {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

footer .notifications h3 {
    font-size: 16px;
}

footer .notifications h2 {
    font-size: 20px;
    margin-bottom: 8px;
}

footer .notifications p {
    max-width: 400px;
}


@media (max-width: 1200px) {
    footer .container {
        justify-content: space-between;
        width: 85%;
    }   
}

@media (max-width: 800px) {
    footer .container {
        width: 95%;
    }

    footer .notifications h2 {
        font-size: 20px;

    }
}

@media (max-width: 601px) {
    footer .container {
        display: flex;
        flex-direction: column;
        grid-gap: 24px;
        gap: 24px;
        align-items: center;
    }

    footer .links {
        text-align: center;
        margin-bottom: 24px;
    }

    footer .notifications {
        text-align: center;
    }

    footer ul {
        grid-gap: 24px;
        gap: 24px;
    }
}
